<style lang="less">
@color: #FFC124;
@page_margin: 30px;
.pages_box{
	text-align: center;
	margin: 2*@page_margin 0 @page_margin 0;
	li{
		display: inline-block;
		background-color: #FFF;
		border: solid 1px @color;
		padding: 10px;
    	margin: 0 15px;
    	a{
    		color: @color;
    	}
	}
}
</style>

<template>
	<ul class="pages_box">
		<li v-for="its in pages" class="item">
			<a href="javascript:;" v-text="its.page"></a>
		</li>
	</ul> 
</template>

<script >
	export default {
		data() {
			return {
				pages : [{
					page: '1'
				},{
					page: '2'
				},{
					page: '3'
				},{
					page: '4'
				},{
					page: '...'
				},{
					page: 'n'
				},{
					page: '上一页'
				},{
					page: '下一页'
				}]
			}
		}
	}
</script>


